import React from 'react';
import styled from 'styled-components';


const Form = () => {
    return(
        <div>
            <FormContainer>
                <h1>Contact me!</h1>
                <h3>(from this shitty form with no style and no padding)</h3>
                <div className = "form">
                    <input type = "text" placeholder = "name"/>
                    <input type="email" placeholder = "e-mail" required />
                    <input className = "tellme" type="text" placeholder = "Tell me something!" required />
                    <div className="buttoni">
                    <button className ="buttonio">Submit</button>
                    </div>
                </div>
            </FormContainer>
            <br/><br/><br/>
            <br/><br/><br/>
            <hr></hr>
            <br/><br/>
            <Footer>Copyright 2020 Marc Eugeni. Made wih love with <a href="https://reactjs.org/" target="_blank">React</a></Footer>
            <br/><br/>
         </div>
    );
}

export default Form;


//Styled Components

const Footer = styled.div`
    color: #cccccc;
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
`

const FormContainer = styled.div`
    color: #cccccc;
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;

    .form {
        display: flex;
        flex-direction: column;
        justify-items: center;
        align-items: center;
        padding: 3rem;

    }

    .tellme {
        width: 300px;
        height: 300px;
        border-radius: 2.5%;
    }

    .buttoni {
        padding: 3rem;
       
    }


`
