import React from 'react';
//Icons
import jsLogo from '../img/js-brands.png';
import code from '../img/file-code-regular.png'
import pen from '../img/pen-fancy-solid.png'
import camera from '../img/camera-retro-solid.png'
//Styles
import {Ebout, Description} from '../styles';
import styled from 'styled-components';


const Cards = () => {
    return (
        
            <Ebout>
                <Description>
                    <h2>High <span>quality</span> services</h2>
                    
                    <Cardos>
                        <Cardo>
                            <div className="icon">
                                <img src={code} alt="logo html"/>
                                <p>Web Development</p>
                            </div>
                        </Cardo>
                        <Cardo>
                            <div className="icon">
                            <img src={jsLogo} alt="logo javascript"/>
                                <p>Programming</p>
                            </div>
                        </Cardo>
                        <Cardo>
                            <div className="icon">
                            <img src={pen} alt="logo design"/>
                            <p>UX/UI</p>
                            </div>
                        </Cardo>
                        <Cardo>
                            <div className="icon"> 
                            <img src={camera} alt="logo photo"/>                                
                            </div>
                            <p>Photo</p>
                        </Cardo>
                    </Cardos>
                    
                    
                </Description>
            </Ebout>
        
    )
}

//Styled components component



const Cardos = styled.div`
    display: flex;
    justify-content: space-between;
    padding-top: 100px;


    
`
const Cardo = styled.div`
    img {
        width: 100px;
    }
  

`

export default Cards; 