import {createGlobalStyle} from 'styled-components';

const GlobalStyle = createGlobalStyle`

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: #2E3440;
    font-family: 'Montserrat', sans-serif;
}

button {
    font-weight: bold;
    font-size: 1.1 rem;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 3px solid #4C566A;
    border-radius: 10%;
    background: transparent;
    color: white;
    transition: all 0.5s ease;
    &:hover{
        background-color: #4C566A;
        color: white;
    }

}

h2 {
    font-weight: lighter;
    font-size: 4rem;
}

h3 {
    color: white;

}

h4 {
    font-weight: bold;
    
}

a {
    font-size: 1.1rem;
}

span {
    font-weight: bold;
    color: #4C566A;
}

p {
    padding: 3rem 0rem;
    color: #ccc;
    font-size: 1.4rem;

}

`;

export default GlobalStyle;